.header {

  &-line {
    display: none;
  }

  .logo {
    &-img {
      padding-top: 27px;
      width: 203px;
    }
  }

  .hamburger {
    position: fixed;
    top: 27px;
    right: 20px;
    z-index: 10;

    span {
      display: flex;
      height: 5px;
      width: 40px;
      background-color: $grey;
      margin-bottom: 4px;
      transition: 0.5s all;
      border-radius: 20px;
    }

    &--active {
      span {
        &:nth-child(1) {
          transform: translateY(8px) rotate(45deg);
        }

        &:nth-child(2) {
          display: none;
        }

        &:nth-child(3) {
          transform: translateY(-1px) rotate(-45deg);
        }
      }
    }
  }

  .menu {
    font-family: 'Gilroy', sans-serif;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    top: 0px;
    left: -100%;
    width: 80%;
    height: 100vh;
    padding: 0 20px;
    background-color: white;
    transition: 0.6s all;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.25);
    z-index: 2;
    overflow-y: scroll;
    -webkit-overflow-scrolling:touch; // mobile safari

    &--active {
      left: 0;
    }

    &-socials {
      display: none;
    }

    &-list {
      list-style-type: none;
      margin-top: 64px;
      font-family: 'Gilroy', sans-serif;

      &__item {
        display: flex;
        padding: 15px 0;
        flex-wrap: wrap;

        &:last-child {
          padding-bottom: 0;
        }
      }

      &__link {
        font-family: 'Gilroy', sans-serif;
        color: $black;
        font-size: 16px;
        font-weight: 800;

        &-dropdown {
          font-family: 'Gilroy', sans-serif;
          color: $black;
          font-size: 16px;
          font-weight: 800;
        }
      }
    }

    .e-mail {
      display: none;
    }

    .phone {
      margin-top: 80px;
      align-items: flex-start;
    }

    .bullets {
      display: none;
    }

    .sub {
      &-menu {
        &__list {
          display: none;
          font-family: 'Gilroy', sans-serif;
          font-size: 16px;
          font-weight: 400;
          position: relative;
          background-color: white;
          box-shadow: none;
          z-index: 9;
          &-item {
            padding: 10px 0 0 5px;
          }

          &-link {
            transition: 0.5s all;
            color: $black;

            &:hover {
              color: $blue;
            }
          }
        }

        &__arrow {
          &::after {
            content: url('../../images/icons/bx_bx-down-arrow.svg');
            color: $blue;
            padding-left: 15px;
          }
        }

        .arrow {
          display: none;
          position: absolute;
          right: 0;
          top: 12px;
          width: 0;
          height: 0;
          border-top: 10px solid #fff;
          border-right: 10px solid transparent;
          border-left: 10px solid transparent;
        }
        
        .arrow.active{
          transform: rotate(-180deg);
        }
      }
    }
  }

  .phone {
    display: flex;
    flex-direction: column;
    margin-top: 12px;
    text-align: right;

    &-link {
      font-family: 'Gilroy', sans-serif;
      font-weight: 800;
      font-size: 24px;
      color: black;
    }

    &-workhours {
      font-family: 'Gilroy', sans-serif;
      font-weight: 300;
      padding: 2px 0 6px 0;
    }
  }

}

body.touch .menu-list__item:hover .sub-menu__list {
  display: flex;
  flex-basis: 100%;
  flex-direction: column;
  transition: 0.5s all;
}

body.touch .sub-menu__list.open{
  display: flex;
}

body.touch .arrow {
  display: flex;
}
